import { Container, Grid, makeStyles, Typography } from '@material-ui/core';
import React from 'react';
import { useAllMembers } from 'src/hooks/members/useAllMembers';
import MemberCard from '../Cards/ImageCard/MemberCard';

const useStyles = makeStyles(({ palette, spacing, typography }) => ({
  root: {
    backgroundColor: palette.grey['100'],
    padding: `${spacing(15)}px 0`,
  },
  title: {
    fontWeight: typography.fontWeightBold,
  },
  description: {
    marginBottom: spacing(10),
  },
}));

const MentorSection = () => {
  const styles = useStyles();
  const members = useAllMembers();
  const mentors = members.filter(member => ['Evan', 'Daniel', 'Ryan', 'Yurim'].includes(member.id));

  return (
    <section className={styles.root}>
      <Container>
        <Typography variant="h4" component="h2" gutterBottom={true} className={styles.title}>
          멘토링 프로젝트를 함께할 멘토들이에요
        </Typography>
        <Typography className={styles.description}>
          멘토들이에요 멘토들이에요 멘토들이에요 멘토들이에요 멘토들이에요 멘토들이에요
        </Typography>
        <Grid container spacing={2}>
          {mentors.map(mentor => (
            <Grid item key={mentor.id} xs={12} sm={3}>
              <MemberCard member={mentor} />
            </Grid>
          ))}
        </Grid>
      </Container>
    </section>
  );
};

export default MentorSection;
